import React from 'react'
import TextTransition from "react-text-transition";

const QuoteBox = ({bb} ) => {

    const TEXTS = "bb.author"



    return (
    
        <div className="p">
            {bb.text}
            <br/>
            <span>

    <TextTransition
              text={`${TEXTS}`.split("")[0]}
              className="big"
              inline
            />


            {bb.author}
            </span>
        </div>



        
    )
}

export default QuoteBox
