import {useState, useEffect} from 'react'
import Axios from "axios";
import Loader from 'react-loader-spinner'
import QuoteBox from './QuoteBox'
import {Button, Typography} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import Footer from './Footer'
import 'fontsource-roboto';





const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },

}));

function App() {
  const classes = useStyles();

  const [status, setStatus] = useState({
    text: '',
    author: ''
  })
  
  const updateQuote = ()=> {
    Axios.get('https://breakingbadapi.com/api/quote/random')
    .then((res)=>{
      const [newQuote] = res.data
      const {author, quote} = newQuote
      setStatus({author, text:quote})
      console.log(status)
    })
    .catch((err)=>{
      console.log(err)
    })
  }

useEffect(()=>{
updateQuote()
}, [])

  return (
    <div className="app">
       <img 
        src="https://static.displate.com/857x1200/displate/2019-10-20/2ee5d2317d146b0d7d812937bc5b704b_060f642437f0a302a2f7d23767847f13.jpg"
        alt="logo"
      />
  {!status ?   (<Loader type="TailSpin" color="#00BFFF" height={80} width={80} /> ):( <> <QuoteBox bb ={status}/>
  <Button variant="contained" color="primary"  size="large" className={classes.margin}  onClick={() => updateQuote()}><Typography  variant="button" display="block" gutterBottom>Get Another</Typography></Button>
  <Footer/>
  </>)
  }
    </div>
  );
}

export default App;
